footer {
	padding-top: 2em;
	background: #3C3C3C;

	h4 {
		widows: 100%;
		color: #fff;
		font-size: 0.9em;
		text-transform: uppercase;
	}
	
	a {
		color: #9e9e9e;
		font-size: 0.85em;

		&:hover {
			color: #fff;
		}
	}
	
}

.copyright {
	padding: 1em 0;
	color: #999;
	font-size: .8em;
}

.languages {
	a {
		display: block;

		img {
			transition: transform 200ms;
			margin: .7em 4px;

			&:hover {
				transform: scale(1.2);
				transition: transform 200ms;
			}
		}
	}
}


